import React, { useEffect, useState, useCallback, useMemo } from 'react';
import { StaticContext } from 'react-router';
import { Redirect, Link, RouteComponentProps } from 'react-router-dom';
import FacebookLogin from 'react-facebook-login';

import styles from '../css/home.module.css';
import { GET, cookies, POST, lichess, lichessCallback } from '../utils/requests';
import { TournamentStatus, ProfileData, FacebookCallback } from '../interfaces';
import { FaAngleRight, FaAngleLeft, FaCheckCircle, FaSyncAlt, FaLink } from 'react-icons/fa';
import { TournamentSettings } from '../resources/settings';
import contactFields from '../resources/contact';
import SectionBreak from '../components/SectionBreak';

export default function User(props: RouteComponentProps<any, StaticContext, any> & {
	profile: ProfileData
	updateProfile: () => void
	endSession: () => Promise<void>
}) {

	const [win, setWindow] = useState(null as Window | null);
	const [redirect, setRedirect] = useState(false);
	const [tournaments, setTournaments] = useState([] as (TournamentStatus & { setting: TournamentSettings })[]);

	const updateTournaments = useCallback(() => GET({ url: '/profile/tournaments' })
		.then(setTournaments), [setTournaments]);

	useEffect(() => {
		updateTournaments();
	}, [updateTournaments]);

	const [sessionID, setSession] = useState('');
	useEffect(() => {
		(async () => {
			let c = await cookies.getAll();
			setSession(c.sessionID);
		})();
	}, [setSession]);
	const href = useMemo(() => {
		const base = 'https://oauth.lichess.org/oauth/authorize';
		const scopes = [
			['email', 'read'],
			['challenge', 'read'],
			['challenge', 'write']
		] as string[][];
		const params = {
			response_type: 'code',
			client_id: lichess,
			redirect_uri: lichessCallback,
			scope: scopes.map(scope => scope.join(':')).join(' '),
			state: `${sessionID.slice(0, 8)}.referrer=${props.location.state.currentPath}`
		}
		return `${base}?${Object.entries(params).map(p => p.join('=')).join('&')}`;
	}, [sessionID, props.location.state]);

	const handleSync = useCallback(async (account: string) => {
		switch (account) {
			case 'lichess': {
				await POST({ url: ['profile', 'updateFromLichess'].join('/') })
					.finally(props.updateProfile)
			}
		}
	}, [props.updateProfile]);

	const handleClose = useCallback((myWin: Window) => {
		if (!myWin.closed) return;
		setWindow(null);
		props.updateProfile();
	}, [props.updateProfile, setWindow]);
	useEffect(() => {
		if (!win) return;
		let x = setInterval(() => handleClose(win), 1000);
		return () => clearInterval(x);
	}, [win, handleClose]);

	const handleFacebook = useCallback((res: FacebookCallback) => {
		POST({
			url: '/profile/linkFacebookAccount',
			data: {
				id: res.userID,
				name: res.name,
				email: res.id,
				accessToken: res.accessToken
			}
		})
			.then(props.updateProfile);
	}, [props.updateProfile]);

	const [yourTournaments, showYourTournaments] = useState(true);

	if (props.location.search.startsWith('?user=')) window.close();
	if (redirect) return <Redirect to='/' />;

	return (<>
        <div id='main' className={'animated-' + props.location.state?.direction}>
			<div className={styles.container}>
				<div className={styles.title}>
					<div className={['container', styles.info].join(' ')}>
						<h1><span className='bold'>{props.profile.username}</span></h1>						
						<div className={styles.details}>
							<div>Email: {props.profile.email}</div>
							<div>Account creation: {new Date(props.profile.createdAt).toString().slice(0, 15)}</div>
							<div>Tournaments: {tournaments.length}</div>
							<div>Public tournaments: {tournaments.filter(t => t.setting && t.setting.public).length}</div>
						</div>
						<div className={styles.buttons}>
							<div className={styles.row}>								
								<a href='https://beta.scorchapp.co.uk' rel='noopener noreferrer' target='_blank' className={['button', 'link', styles.new].join(' ')}>
									Create New Tournament
									<FaAngleRight />
								</a>							
								<div className={['button', 'link', styles.logout].join(' ')} onClick={() => {
									props.endSession().then(() => setRedirect(true));
								}}>
									Log out
									<FaAngleRight />
								</div>
							</div>
							<div className={styles.row}>
								<a
									id='lichess'
									href={props.profile.lichess ? (contactFields.lichess.link as string).replace('|', props.profile.lichess.username) : href}
									className={['button', 'link', styles.account].join(' ')}
									rel='noopener noreferrer'
									target='_blank'
								>
									<img src={contactFields.lichess.icon[1] as string} alt='lichess' />
									{props.profile.lichess?.username || 'Link Lichess account'}
									{props.profile.lichess?.verified ? <FaCheckCircle className='verified' /> : null}
									{!props.profile.lichess ? <FaAngleRight /> : null }
								</a>
								{props.profile.lichess ?
									props.profile.lichess?.verified ?
										<div id='lichess' className={['button', 'link', styles.sync].join(' ')} onClick={() => handleSync('lichess')}>
											<FaSyncAlt />
										</div> :
										<div id='lichess' className={['button', 'link', styles.sync].join(' ')} onClick={() => setWindow(window.open(href))}>
											<FaLink />
										</div> :
									null
								}
							</div>							
							<div className={styles.row}>
								{props.profile.facebook ?
									<a
										id='facebook'
										href={props.profile.facebook ? (contactFields.facebook.link as string).replace('|', props.profile.facebook.name) : href}
										className={['button', 'link', styles.account].join(' ')}
										rel='noopener noreferrer'
										target='_blank'
									>
										{contactFields.facebook.icon[1]}
										{props.profile.facebook.name}
										<FaCheckCircle className='verified' />
									</a> :
									<FacebookLogin
										appId='3423958477620478'
										fields='name,email'
										scope='public_profile'
										callback={handleFacebook}
									/>
								}
								
							</div>
						</div>
					</div>
				</div>
				{
					tournaments.length ?
					<>
						<SectionBreak name='YOUR TOURNAMENTS' expanded={yourTournaments} toggle={showYourTournaments} />
						<div className={['container', styles.yourTournaments, yourTournaments ? '' : 'collapsed'].join(' ')}>
							<table className={[styles.tableAnnounce].join(' ')}>
								<thead>
									<tr>
										<th style={{width: '20px' }}>Manage</th>
										<th>Name</th>
										<th>Started</th>
										<th>Round</th>
										<th>View</th>
									</tr>
								</thead>
								<tbody>
									{tournaments.sort((a, b) => new Date(b.updatedAt).valueOf() - new Date(a.updatedAt).valueOf()).map((tourn, i) => {
										let dateStarted = new Date(tourn.createdAt).toString();
										return (
											<tr key={[tourn.id, i].join('.')} className={styles.rowLink}>
												<td><a className={styles.tournamentLink} href='https://beta.scorchapp.co.uk'>
													<FaAngleLeft />
												</a></td>
												<td>
													{tourn.name || tourn.id}
												</td>
												<td>
													{dateStarted.slice(0, 11)}
												</td>
												<td>
													{tourn.round}
												</td>
												<td>
													{tourn.setting && tourn.setting.public ?
													<Link className={styles.tournamentLink} to={{pathname: '/' + tourn.id, state: {from: props.location.pathname}}}>
														<FaAngleRight />
													</Link>
													: null}
												</td>
											</tr>
										);
									})}				
								</tbody>
							</table>
						</div>
					</>:
					null
				}
			</div>				
		</div>
	</>)
}