import React, { useMemo, useContext } from 'react';
import { Link } from 'react-router-dom';
import { EnhancedPlayer, Team } from '../../interfaces';
import { Player } from '../../models';

import { columns } from '../../resources/columns';
import { FaEllipsisH } from 'react-icons/fa';
import styles from '../../css/profile.module.css';
import { GET } from '../../utils/requests';
import { LoginContext } from '../../App';
import contactFields from '../../resources/contact';
import { capitalise } from '../../utils/prototype';

export async function fetchNames (data: [string, string][]) {
	let sorted = data.reduce((acc, curr) => {
		if (!acc[curr[1]]) acc[curr[1]] = [] as string[];
		acc[curr[1]].push(curr[0]);
		return acc;
	}, {} as {[key: string]: string[]});
	let promises: Promise<Player[]>[] = [];
	for (let [id, ids] of Object.entries(sorted)) {
		promises.push(GET({
			url: ['tournament', id, 'fetchPlayers'].join('/'),
			params: { ids: ids.join(',') }
		}))
	}
	let names = {} as {[key: string]: string}
	for (let list of await Promise.all(promises)) {
		for (let p of list) {
			names[p.id] = p.firstName;	//TODO ON BACKEND
		}
	}
	return names;
}

export function renderTournament(p: EnhancedPlayer, visible: boolean, playerNames: {[key: string]: string}): [JSX.Element, [string, string][]] {	

	const player = new Player(p, p.tournament.round, p.tournament.setting);
	const shouldFetch = [] as [string, string][];
	let totalRounds = typeof p.tournament.setting.totalRounds as string | number !== 'string' ?
		p.tournament.setting.totalRounds !== Infinity ?
			p.tournament.setting.totalRounds :
		p.histories.length + 1 :
	p.histories.length + 1;
	let histories = p.histories.slice(0);
	for (let i = 0; i <= totalRounds; i++) if (!histories[i]) histories[i] = null as any;
	
	return [
		<section className={['container', styles.background, !visible ? 'collapsed' : ''].join(' ')}>
			<div className={styles.stage}>
				<Link to={`/${p.tournament.setting.vanityURL || p.tournament.id}`} className={[styles.name, styles.box].join(' ')}>{p.tournament.name}</Link>
				<div className={[styles.scores, styles.box].join(' ')}>
					{Object.entries(columns.post).map(([k, meta], i) => {
						if (meta.show && !meta.show(player, i, p.tournament.setting, 'results')) return null;
						return <div className={styles.row}>
							<div className={styles.key}>{meta.brief || meta.mobile}</div>
							<div className={styles.value}>
								{meta.convert ?
									meta.convert(player[k as keyof Player], player, p.tournament.setting, 'results') :
									player[k as keyof Player]?.toString()}
								</div>
						</div>;
					})}
				</div>
				<Link to={`/${p.tournament.setting.vanityURL || p.tournament.id}`} className={[styles.currRound, styles.box, 'header-button'].join(' ')}>
					Round: {p.tournament.round}
				</Link>
				<div className={[styles.nextRound, styles.box, 'header-button'].join(' ')}>
					Time: {p.tournament.setting.timeControl}
				</div>
				<div className={[styles.history, styles.box].join(' ')} style={p.tournament.setting.totalRounds < 10 ? {gridTemplateRows: `1.5em repeat(${p.tournament.setting.totalRounds}, auto)`} : {}}>
					<div className={styles.subtitle}>
						Results
					</div>
					{histories.map((h, i) => {
						if (!i) return null;
						if (!h) h = {} as any;
						let { id, colour } = h;
						let game = h.game as number | null;
						let match = h.match as number | null;
						let showGame = h.game !== null && p.tournament.setting.displayPoints === 'game';
						let w = colour === 'W' ? p.id : h.id;
						let b = colour === 'W' ? h.id : p.id;
						if (id && !(id in playerNames)) shouldFetch.push([id, p.tournament.id]);
						let data = [
							i,
							playerNames[id] || id,
							colour || '\u200b',
							game !== null ? game : <FaEllipsisH />,
							match !== null ? match : <FaEllipsisH />
						]
						return (
							<div className={[styles.resultsRow, showGame ? '' : styles.small].join(' ')} key={['row', 'history', i].join('.')}>
								{data.map((entry, j) => {
									let className = j === 1 ? styles.playerName : styles.fieldBox;
									let to = j === 1 ?`/${p.tournament.setting.competitors === 'team' ? 'team' : 'individual'}/${id}`  : `/${p.tournament.id}/pairings/${i}#${w}.${b}`;
									if (j === 3 && !showGame) return null;
									if (!id) {
										return <div className={className} key={['entry', j].join('.')}>{entry}</div>
									} else {
										return <Link to={to} className={className} key={['entry', j].join('.')}>{entry}</Link>
									}
								})}
							</div>
						);
					})}
				</div>
			</div>
		</section>,
		shouldFetch
	];

}

export function Contact({ player, notes }: {player: Team, notes: (string | null)[]}) {

	const loggedIn = useContext(LoginContext);
	const Notes = useMemo(() => {
		return <div className={styles.notes}>
			{notes.map(line => line ? <div>{line}</div> : null)}
		</div>
	}, [notes]);

	if (!loggedIn) {
		return <Link to='/login'>Sign in to view contact details</Link>;
	}
	if (!player.contact || !Object.values(player.contact).length) {
		return <div>No contact details registered</div>;
	}

	return (
		<>
			{Object.entries(contactFields).map(([k, meta], i) => {
				let v = meta.value(player as any);
				if (!v) return null;
				if (typeof v === 'string' && v.startsWith('http')) v = v.split('/').pop();
				if (!meta.link) {
					return <div id={k} className={['button', 'link'].join(' ')} key={[k, i].join('i')}>
						{meta.icon[0] === 'img' ?
							<img src={meta.icon[1] as string} alt={k} /> :
							meta.icon[1]
						}
						{v}
					</div>
				} else {
					return <a
						id={k}
						href={meta.link.replace('|', v || '')}
						className={['button', 'link'].join(' ')}
						key={[k, i].join('i')}
						rel='noopener noreferrer'
						target='_blank'
					>
						{meta.icon[0] === 'img' ?
							<img src={meta.icon[1] as string} alt={k} /> :
							meta.icon[1]
						}
						{meta.name || capitalise(k)}
					</a>
				}
			})}
			{notes.length && notes.some(n => n) ? Notes : null}
		</>
	)
}