import React, { ReactElement, useCallback, useEffect, useState, useContext} from 'react';
import { Redirect, Link } from 'react-router-dom';
import { PairingsProps } from './Pairings';
import styles from '../css/pairings.module.css';
import { GET } from '../utils/requests';
import { TournamentSettings } from '../resources/settings';
import { FaAngleDoubleRight, FaPlusCircle } from 'react-icons/fa';
import { AiOutlineTable } from 'react-icons/ai';
import { IoIosStats } from 'react-icons/io';
import { watch } from '../assets/icons';
import { LoginContext } from '../App';

export function ModeSwitcher(props: {
	id: string,
	section: 'pairings' | 'standings' | 'stats' | 'tv'
	settings: TournamentSettings
	round: number
	loaded: {[key: string]: boolean}
}): ReactElement {

	const loggedIn = useContext(LoginContext);
	const [shouldRedirect, redirectTo] = useState('');

	const handleJoin = useCallback(() => {
		if (!loggedIn) return redirectTo('/login');
		return null;
	}, [loggedIn, redirectTo]);

	if (shouldRedirect) return <Redirect to={shouldRedirect} />;

	return <>
		<div className={[styles.modeSwitcher].join(' ')}>
			{props.loaded.all ?
				props.round ? <Link to={`/${props.id}/pairings`} className={['header-button', props.section === 'pairings' ? 'selected' : ''].join(' ')}>
					<div><FaAngleDoubleRight />Pairings</div>
				</Link> : null :
			<div className='header-button'>{'\u200b'}</div>}
			{props.loaded.all ?
				<Link to={`/${props.id}/standings`} className={['header-button', props.section === 'standings' ? 'selected' : ''].join(' ')}>
					<div><AiOutlineTable /> Standings</div>
				</Link> :
			<div className='header-button'>{'\u200b'}</div>}
			{props.loaded.all ?
				<Link to={`/${props.id}/stats`} className={['header-button', props.section === 'stats' ? 'selected' : ''].join(' ')}>
					<div><IoIosStats /> Stats</div>
				</Link> :
			<div className='header-button'>{'\u200b'}</div>}
			{props.loaded.all ?
				props.settings.streamURL ?
					<Link to={`/${props.id}/tv`} className={['header-button', 'stream-link', props.section === 'stats' ? 'selected' : '', 'twitch-link'].join(' ')}>
						<div><img src={watch} alt='tv'/> TV</div>
					</Link>
				: null :
			<div className={['header-button', 'stream-link', 'twitch-link'].join(' ')}>{'\u200b'}</div>}
		</div>
		{props.settings.allowRegistration && props.settings.allowRegistration !== 'closed' ? <div className={['header-button', styles.join].join(' ')} onClick={handleJoin}>
			<div><FaPlusCircle />Join this tournament</div>
		</div> : null}
	</>;
}

export function RoundSwitcher(props: PairingsProps & {
	section: 'pairings' | 'standings' | 'stats' | 'tv',
	round: number
	loaded: {[key: string]: boolean}
}): ReactElement {

	const [roundTimes, setRoundTimes] = useState([]);
	const fetchRoundTimes = useCallback(() => GET({ url: `tournament/${props.id}/fetchRounds` })
		.then(setRoundTimes), [props.id, setRoundTimes]);
	useEffect(() => {
		fetchRoundTimes();
	}, [fetchRoundTimes]);

	let rounds = [];
	let totalRounds = props.settings.totalRounds === Infinity || props.settings.totalRounds === 'Infinity' ? 1 : props.settings.totalRounds;
	for (let i = 1; i < totalRounds + 1; i++) {
		let date = roundTimes[i] ? new Date(roundTimes[i]).toString() : '';
		rounds.push(
			<Link
				className={['header-button', styles.pairingRound, props.round === i ? styles.roundSelected : '', i > props.status.round ? styles.nullRound : ''].join(' ')}
				to={`/${props.id}/${props.section}/${i}`}
				key={['round', i].join('.')}
			>
			<div className='full-text'>
				{props.loaded.all ?
					<>
						<h4>Round {i}</h4>
						{roundTimes[i] ? 
							<h5>{date.substring(0, 11) + date.substring(16, 21)}</h5> :
							null
						}
					</>
				: '\u200b'}
				</div>
			</Link>
		);
	}
	if (props.loaded.status && props.section === 'pairings' && props.round && !props.status.active) rounds.push(
		<Link
			className={['header-button', styles.pairingRound, props.round === totalRounds + 1 ? styles.roundSelected : ''].join(' ')}
			to={`/${props.id}/${props.section}/results`}
		>
			<div className='full-text'>
				<h4>Results</h4>
				{roundTimes[totalRounds + 1] ? <h5>{new Date(roundTimes[totalRounds + 1]).toString().substring(0, 11) + new Date(roundTimes[totalRounds + 1]).toString().substring(16, 21)}</h5> : null}
			</div>				
		</Link>
	)

	return (
		<div className={['container', 'bodyContainer'].join(' ')}>
			{rounds.length ?
				<div className='button-list'>
					{rounds}
				</div> :
			null}
		</div>
	);
}