import React from 'react';
import { Player as PlayerData } from '../models';
import { TournamentSettings } from '../interfaces';
import { flags } from '../config.json';

export interface ColumnData {
	id?: (entry: PlayerData, i: number) => string
	name: string | ((key: string, i: number) => string)
	className: 'blank' | 'playerName' | 'flag' | 'locked' | 'rating' | 'unset' | 'edit' | ((v: string, i: number, settings: TournamentSettings) => string),
	sort?: false | ((a: any, b: any) => number),
	show?: (entry: PlayerData, i: number, settings: TournamentSettings, h?: string) => boolean
	convert?: (v: any, p: PlayerData, s: TournamentSettings, h: string) => string | JSX.Element
	postConvert?: (v: string, i: number) => string
	mobile?: string
	brief?: string
}

export const columns = {
	pre: {
		rank: {
			mobile: 'Rank',
			name: '',
			className: 'blank',
			sort: false,
			postConvert: (v: string, i: number): string => (1 + i).toString()
		},
		name: {
			name: 'Name',
			className: 'playerName',
			id: (p: PlayerData): string => p.id,
			sort: (a: string, b: string) => {
				if (a > b) return 1;
				if (a < b) return -1;
				return 0;
			},
		},
		nationality: {
			name: 'Fed',
			mobile: 'Federation',
			className: 'flag',
			convert: (v: string) => v ? <img src={flags.replace('|', v)} alt={v} /> : null
		},
		rating: {
			name: 'Rating',
			className: 'locked',
			show: (e: PlayerData, i: number, s: TournamentSettings) => s.showRatings
		}
	},
	roundColumn: {
		name: (key: string, i: number): string => key.slice(-1),
		className: (v: string, i: number, s: TournamentSettings) => {
			if (v === '*') return 'edit';
			if (v === '') return 'unset';
			else return 'locked';
		},
		convert: (v: string, p: PlayerData, s: TournamentSettings, h: string) => {
			if (v === '') return '';
			else if (v === '*') return '*';
			let bonus = false;
			let round = Number(h.slice(-1));
			if (p.bonuses) for (let b of p.bonuses) if (b.round === round) { v += b.MP; bonus = true };
			let nb = parseFloat(v);
			let output: string;
			if (nb === 0.5) output = '';
			else output = nb.toString();
			if (v.endsWith('b')) output += 'b';
			if (bonus) output += '*';
			return output;
		},
		sort: (a: string, b: string): number => {
			if (a === b) return 0;
			if (a === '') return 1;
			else if (b === '') return -1;
			if (a === '*') return 1;
			else if (b === '*') return -1;
			let [aN, bN] = [parseInt(a), parseInt(b)];
			if (a.toString().endsWith('b')) aN -= 0.001;
			if (b.toString().endsWith('b')) bN -= 0.001;
			return bN - aN;
		}
	},
	post: {
		score: {
			mobile: 'Match Points',
			name: 'MP',
			className: 'locked',
			convert: (v: number, p: PlayerData): string => Math.floor(v) === v ? v.toFixed() : v === 0.5 ? '' : Math.floor(v).toFixed() + ''
		},
		gamePoints: {
			mobile: 'Game Points',
			name: 'GP',
			className: 'locked',
			convert: (v: number): string => v.toFixed(1)
		},
		gameDifference: {
			mobile: 'Game Point Difference',
			brief: 'Game Point +/-',
			name: 'G. +/-',
			className: 'locked',
			convert: (v: number): string => {
				if (!v) return '';
				if (v > 0) return '+' + v.toFixed(1);
				else return v.toFixed(1)
			},
		},
		gamesPlayed: {
			mobile: 'Games Played',
			name: 'P',
			className: 'locked',
			convert: (v: number): string => v.toString()
		},
		matchPercentage: {
			mobile: 'Match Point Percentage',
			brief: 'Match Point +/-',
			name: 'M. %',
			className: 'rating',
			convert: (v: number): string => v.toFixed()
		},
		gamePercentage: {
			mobile: 'Game Point Percentage',
			brief: 'Game Point %',
			name: 'G. %',
			className: 'rating',
			convert: (v: number): string => v.toFixed()
		},
		performanceRating: {
			mobile: 'Performance Rating',
			brief: 'Perf. Rating',
			name: 'Perf',
			className: 'rating',
			convert: (v: number): string => v.toFixed(),
			show: (entry: PlayerData, i: number, s: TournamentSettings) => s.showRatings
		},
		ratingDifference: {
			mobile: 'Performance Rating Difference',
			brief: 'Perf. Rating +/-',
			name: 'P. +/-',
			className: 'rating',
			convert: (v: number): string => {
				if (!v) return '';
				if (v > 0) return '+' + v.toString();
				else return v.toString()
			},
			show: (entry: PlayerData, i: number, s: TournamentSettings) => s.showRatings
		},
		expectedScore: {
			mobile: 'Expected Score',
			name: 'Expected',
			className: 'rating',
			convert: (v: number, p: PlayerData): string => v.toFixed(1) + ' / ' + p.played,
			show: (entry: PlayerData, i: number, s: TournamentSettings) => s.showRatings
		},
		expectedDifference: {
			mobile: 'Score to Expected',
			brief: 'Expected +/-',
			name: 'E. +/-',
			className: 'rating',
			convert: (v: number): string => {
				if (typeof v === 'undefined') return '';
				if (v > 0) return '+' + v.toFixed(2);
				else return v.toFixed(2)
			},
			show: (entry: PlayerData, i: number, s: TournamentSettings) => s.showRatings
		},
		wins: {
			mobile: 'Wins',
			name: 'W',
			className: 'locked',
			show: (e, i, s, p: string) => p !== 'results',
			convert: (v: number): string => v.toString()
		},
		draws: {
			mobile: 'Draws',
			name: 'D',
			className: 'locked',
			show: (e, i, s, p: string) => p !== 'results',
			convert: (v: number): string => v.toString()
		},
		losses: {
			mobile: 'Losses',
			name: 'L',
			className: 'locked',
			show: (e, i, s, p: string) => p !== 'results',
			convert: (v: number): string => v.toString()
		},
		played: {
			mobile: 'Played',
			name: 'P',
			className: 'locked',
			show: (e, i, s, p: string) => p !== 'results',
			convert: (v: number): string => v.toString()
		}
	}
} as {
	pre: {
		[key: string]: ColumnData
	},
	roundColumn: ColumnData
	post: {
		[key: string]: ColumnData
	}

}