import React, { useReducer, useCallback, useEffect, ReactElement, useState, useMemo, useRef, RefObject } from 'react';
import ChessWebAPI, { BoardData, MatchData, Game, ChessPlayer } from 'chess-web-api';
import * as regexes from '../utils/regexes';
import styles from '../css/chesscom.module.css';
import '../css/react-pgn-viewer.css';
import { FaChessPawn, FaChessKing, FaChessKnight, FaChessBishop, FaChessRook, FaChessQueen, FaChessBoard, FaAngleLeft, FaAngleRight } from 'react-icons/fa';
import { AiOutlineLoading3Quarters } from 'react-icons/ai'
import PGN from '../components/react-pgn-viewer/';
