export const Chess = new ChessWebAPI({ queue: true });

export async function loadChessComGames(id: number, board: number): Promise<BoardData> {
    return new Promise((res, rej) => {
        Chess.dispatch(Chess.getTeamLiveMatchBoard, (data: { body: BoardData }, error) => {
            if (error) return rej(error);
            res(data.body);
        }, [id, board]);
    });
}

export async function loadChessComMatch(id: number): Promise<MatchData> {
    return new Promise((res, rej) => {
        Chess.dispatch(Chess.getTeamLiveMatch, (data: { body: MatchData }, error) => {
            if (error) return rej(error);
            res(data.body);
        }, [id]);
    });
}

export function colour(piece: string): 'W' | 'B' {
    if (piece.toLowerCase() === piece) return 'B';
    return 'W';
}

const Piece: Map<string, ReactElement> = new Map([
    ['p', <FaChessPawn />],
    ['n', <FaChessKnight />],
    ['b', <FaChessBishop />],
    ['r', <FaChessRook />],
    ['q', <FaChessQueen />],
    ['k', <FaChessKing />]
]);

interface ChessComProps {
    matchURL: string
	enforceBoards: number | undefined
	round: number
}

export default function ChessCom(props: ChessComProps) {

	const [boards, setBoards] = useState(20);
	useEffect(() => {
		if (props.enforceBoards) setBoards(props.enforceBoards);
	}, [props.enforceBoards, setBoards]);
    const [games, setGame] = useReducer((state: {games: Game[], boardScores: {[key: string]: number}}[], action: {
        board: number,
        boardScores: {[key: string]: number}
        games: Game[]
    }) => {
        let arr = state.slice(0);
        arr[action.board] = {
            boardScores: action.boardScores,
            games: action.games
        };
        return arr;
    }, []);
    const [id, setID] = useState(0);
    const [teams, setTeam] = useReducer((state: [string, string], action: {
        name: string
        i: number
    }) => {
        state[action.i] = action.name;
        return state;
    }, ['', '']);
    const [teamDict, setTeamDict] = useReducer((state: {[key: string]: number}, action: {player: ChessPlayer, team: number}) => {
        let res = Object.assign({}, state);
        res[action.player.username.toLowerCase()] = action.team;
        return res;
    }, {});
    const [matchFinished, setMatchFinished] = useState(false);
    const [boardsFinished, setFinished] = useReducer((state: boolean[], action: {
       board: number,
       set: boolean
    }) => {
        let arr = state.slice(0);
        arr[action.board] = action.set;
        return arr;
	}, []);
	const [gamesLength, setGamesLength] = useState(0);

    const fetchChessCom = useCallback(async () => {
        let matches = props.matchURL.match(regexes.chessCom);
        if (!matches) return console.error('Invalid URL: ', props.matchURL);
        let id = Number(matches[1]);
        try {
			let body = await loadChessComMatch(id);
            if (body.boards) setBoards(body.boards);
            if (body.status === 'finished') setMatchFinished(true);
            for (let t of Object.values(body.teams)) {
                let i = teams.findIndex(v => !v || v === t.name);
                setTeam({ name: t.name, i });
                for (let player of t.players) {
                    setTeamDict({
                        player,
                        team: i
                    });
                }
            }
        } catch (e) {
            console.error(e);
        }
        setID(id);
    }, [props.matchURL, teams]);

    useEffect(() => {
        fetchChessCom();
    }, [fetchChessCom]);

    const ping = useCallback(async () => {
		if (!id) return;
		if (matchFinished && games.length && !boards) return;
        for (let i = 1; i < boards + 1; i++) {
            if (boardsFinished[i]) continue;
            let x = loadChessComGames(id, i)
                .then((fetchedGames) => {
                    setGame({
                        games: fetchedGames.games,
                        boardScores: fetchedGames.board_scores,
                        board: i
                    });
					let lastGame = fetchedGames.games[fetchedGames.games.length - 1];
					if (!lastGame) throw new Error('No game data given');
					if (fetchedGames.games.length > gamesLength) setGamesLength(fetchedGames.games.length);
                    else if (fetchedGames.games.length >= gamesLength && lastGame.end_time) setFinished({board: i, set: true});
                    return true;
                })
                .catch((e) => {
					console.error(e);
                    if (e.message.includes('doesn\'t have board')) {
						if (i !== 1 || games.length > 2) {
							if (i === 2) setBoards(i - 2);
							else setBoards(i - 1);
							return false;
						}
					}
                    return true;
                });
            if (boards === (props.enforceBoards || 20)) {
                if (await x === false) break;
            }
        }

    }, [id, boards, matchFinished, boardsFinished, games.length, props.enforceBoards, gamesLength, setGamesLength])

    useEffect(() => {
        let x = setInterval(ping, 1000);
        return () => clearInterval(x);
	}, [ping]);

	const [focused, setFocused] = useState(0);

    if (!games.length && !boards) return <div>Match has not yet started!</div>

	if (!games.length) return <div className={styles.loading}><AiOutlineLoading3Quarters /></div>

	let style = games.length ? {} : {gridTemplateColumns: '1fr'}

    return (
        <div className={styles.container} style={style}>
			{games.map((pairing, i) => <GameViewer
				key={['game', i].join('.')}
				pairing={pairing}
				teamDict={teamDict}
				teams={teams}
				round={props.round}
				board={i}
				focused={focused === i}
				setFocused={() => setFocused(i)}
			/>)}
        </div>
    );
}

interface ViewerProps {
	pairing: { games: Game[], boardScores: {[key: string]: number}}
	teamDict: {[key: string]: number}
	board: number
	round: number
	teams: [string, string]
	focused: boolean
	setFocused: () => void
}

function GameViewer(props: ViewerProps) {

	const scores = useMemo(() => Object.entries(props.pairing.boardScores), [props.pairing.boardScores]);
	const [currIndex, setIndex] = useState(props.pairing.games.length - 1);
	const gameRef = useRef(null) as RefObject<HTMLDivElement>;

	let rendered = useMemo(() => props.pairing.games.map((g, i) => <SingleGame
		g={g}
		round={props.round}
		board={props.board}
		nb={i}
		teams={props.teams}
		teamDict={props.teamDict}
	/>), [props.pairing.games, props.board, props.round, props.teams, props.teamDict]);

	const g = props.pairing.games[currIndex];
	const flip = g && props.teamDict[g.black.username.toLowerCase()]; 
	let _scores = flip ? scores.reverse() : scores;

	useEffect(() => {
		window.dispatchEvent(new Event('resize'));
	});

	const buttons = useMemo(() => {
		let div = gameRef.current;
		if (!div) return [];
		let buttons = Array.from(div.querySelectorAll('.visible .pgnViewerFooter > div'));
		return buttons;
	}, [gameRef.current])

	const handleKeyPress = useCallback((e: KeyboardEvent) => {
		switch (e.keyCode) {
			case 37: {	//left
				let button = buttons[1] as HTMLDivElement;
				if (!button) break;
				button.click();
				e.preventDefault();
				break;
			}
			case 39: {	//right
				let button = buttons[2] as HTMLDivElement;
				if (!button) break;
				button.click();
				e.preventDefault();
				break;
			}
			case 38: { //up
				let button = buttons[0] as HTMLDivElement;
				if (!button) break;
				button.click();
				e.preventDefault();
				break;
			}
			case 40: { //down
				let button = buttons[3] as HTMLDivElement;
				if (!button) break;
				button.click();
				e.preventDefault();
				break;
			}
			case 32: { //space
				let button = buttons[4] as HTMLDivElement;
				if (!button) break;
				button.click();
				if (gameRef.current) gameRef.current.scrollIntoView({ behavior: 'smooth' })
				e.preventDefault();
				break;				
			}
			case 70: { //f
				let button = buttons[5] as HTMLDivElement;
				if (!button) break;
				button.click();
				break;
			}
			case 68: { //d
				let button = buttons[6] as HTMLDivElement;
				if (!button) break;
				button.click();
				e.preventDefault();
				break;
			}
		}
	}, [gameRef.current, buttons]);
	useEffect(() => {
		if (!props.focused) return;
		document.addEventListener('keydown', handleKeyPress);
		return () => document.removeEventListener('keydown', handleKeyPress);
	}, [props.focused, handleKeyPress]);

	const handleClick = useCallback(() => {
		let div = gameRef.current;
		if (!div) return;
		props.setFocused();
	}, [gameRef.current, props.setFocused])

	if (currIndex < 0) return null;

	return (
		<div className={styles.wrapper} onClick={handleClick} ref={gameRef}>
			<div className={styles.boardLabel}>
				<FaAngleLeft className={[styles.pan, currIndex ? styles.active : styles.null].join(' ')} onClick={() => currIndex ? setIndex(currIndex - 1) : null}/>
				<span className='bold'>{_scores[0][1]}</span>
				<a href={'https://www.chess.com/live/game/' + g.url.split('/').pop()} target='_blank' rel='noopener noreferrer'>
					<FaChessBoard />
				</a>
				<span className='bold'>{_scores[1][1]}</span>
				<FaAngleRight className={[styles.pan, currIndex === props.pairing.games.length - 1 ? styles.null : styles.active].join(' ')} onClick={() => currIndex < props.pairing.games.length - 1 ? setIndex(currIndex + 1) : null}/>
			</div>
			{rendered.map((r, i) => <div className={[styles.gameViewWrapper, i === currIndex ? 'visible' : 'hidden'].join(' ')}>
				{r}
			</div>)}
		</div>
	);
}

function SingleGame({g, round, board, nb, teams, teamDict}: {
	g: Game
	round: number
	board: number
	nb: number
	teams: [string, string]
	teamDict: {[key: string]: number}
}) {

	const flip = teamDict[g.black.username.toLowerCase()];    // team 1 is black
	if (!g.pgn) return null;
	let pgn = [
		`[WhiteTeam "${flip ? teams[0] : teams[1]}"]`,
		`[BlackTeam "${flip ? teams[1] : teams[0]}"]`,
		`[Board "${board + 1}"]`,
		`[Game "${nb + 1}"]`,
		g.pgn.replace('[Round "-"]', `[Round "${round}"]`)
	].join('\n');

	return (
		<PGN
			innerHTML={false}
			showCoordinates={true}
			width={'100%'}
			startAtLatest
		>
			{pgn}
		</PGN>
	);
}