import React, { useEffect, useState, useCallback, useContext } from 'react';
import { StaticContext } from 'react-router';
import { Link, RouteComponentProps } from 'react-router-dom';
import { NotifContext } from './Contexts';

import * as regexes from './utils/regexes';
import styles from './css/home.module.css';
import { GET, socket } from './utils/requests';
import { Notif, TournamentStatus } from './interfaces';
import { FaAngleRight } from 'react-icons/fa';
import { GoPrimitiveDot } from 'react-icons/go'
import { MdAnnouncement } from 'react-icons/md';
import { getLogo } from './components/NotifBox';

let connected = 0;

export default function Home(props: RouteComponentProps<any, StaticContext, any>) {

	const [tournaments, setTournaments] = useState([] as TournamentStatus[]);
	const [notifications, setNotifications] = useState([] as Notif[]);
	const select = useContext(NotifContext);

	const updateTournaments = useCallback(() => GET({ url: '/tournaments' })
		.then(setTournaments), [setTournaments]);

	const updateNotifications = useCallback(() => GET({ url: '/notifications' })
			.then((n) => setNotifications(n.sort((a: Notif, b: Notif) => new Date(b.createdAt).valueOf() - new Date(a.createdAt).valueOf()))), [setNotifications]);

	useEffect(() => {
		updateTournaments();
		updateNotifications();
	}, [updateTournaments, updateNotifications]);

	useEffect(() => {
		socket.on('connected_count', (count: number) => connected = count);
	}, []);

	return (<>
        <div id='main' className={'animated-' + props.location.state?.direction}>
			<div className={styles.container}>
				<div className={styles.title}>
					<h1><span className='bold'>Results</span> by ScorchChess</h1>
					<h3>Your universal platform for results and pairings. Attractive. Automated. Effortless.</h3>
				</div>
				<div className={['container', styles.announcements].join(' ')}>
					<table className={[styles.tableAnnounce].join(' ')}>
						<thead>
							<tr>
								<th className={styles.sectionHeader} colSpan={3}>
									Announcements
								</th>
							</tr>
						</thead>
						<tbody>
							{notifications.map((n, i) => {
								let icon = <MdAnnouncement /> as JSX.Element | null;
								let domain = (n.link || '').match(regexes.link);
								if (domain) icon = getLogo(domain[1]);
								return (
									<tr className={styles.notificationRow} key={['notification', i].join('.')}>
										<td>
											<div onClick={() => select(n)}>												
												{n.link ?
													<a className={styles.iconLink} href={n.link} rel='noopener noreferrer' target='_blank'>
														{icon}
													</a> :
													<div>icon</div>
												}												
												<div>
													<span className='bold'>{n.title}</span>
												</div>
												<div className={styles.notifContent}>
													{n.content}
												</div>
												<div className={styles.expando}>
													<FaAngleRight />
												</div>
											</div>
										</td>
									</tr>
								);
							})}
						</tbody>
					</table>
				</div>
				<div className={['container', styles.tournaments].join(' ')}>
					<table className={[styles.tableAnnounce].join(' ')}>
						<thead>
							<tr>
								<th colSpan={4} className={styles.sectionHeader}>
									Public Tournaments
								</th>
							</tr>
							<tr className={styles.sectionHeader}>
								<th>Name</th>
								<th>Started</th>
								<th>Round</th>
								<th />
							</tr>
						</thead>
						<tbody>
							{tournaments.sort((a, b) => new Date(b.updatedAt).valueOf() - new Date(a.updatedAt).valueOf()).map((tourn, i) => {
								let dateStarted = new Date(tourn.createdAt).toString();
								return (
									<tr key={[tourn.id, i].join('.')} className={styles.rowLink}>
										<td><Link className={styles.tournamentLink} to={'/' + tourn.id}>
											{tourn.name || tourn.id}
										</Link></td>
										<td><Link className={styles.tournamentLink} to={'/' + tourn.id}>
											{dateStarted.slice(0, 11)}
										</Link></td>
										<td><Link className={styles.tournamentLink} to={'/' + tourn.id}>
											{tourn.round}
										</Link></td>
										<td><Link className={styles.tournamentLink} to={'/' + tourn.id}>
											<FaAngleRight />
										</Link></td>
									</tr>
								);
							})}				
						</tbody>
					</table>
				</div>
				<div className={socket.connected ? styles.connected : styles.disconnected}>
					<GoPrimitiveDot /> {connected || '-'} users online
				</div>
				<a href='https://beta.scorchapp.co.uk' rel='noopener noreferrer' target='_blank' className={[styles.new].join(' ')}>
					Create New Tournament
					<FaAngleRight />
				</a>
			</div>				
		</div>
	</>)
}