import React, { useEffect, useState, useCallback, ReactElement, useMemo } from 'react';
import { StaticContext } from 'react-router';
import { RouteComponentProps } from 'react-router-dom';

import styles from '../css/profile.module.css';
import { GET } from '../utils/requests';
import { flags } from '../config.json';
import countries from '../assets/countries.json';
import { Team as TeamInterface, EnhancedPlayer } from '../interfaces';
import SectionBreak from '../components/SectionBreak';
import { renderTournament, fetchNames, Contact } from './Profile';
import NotFound from '../404';

export default function PlayerPage(props: RouteComponentProps<any, StaticContext, any> & {
	id: string
}): ReactElement | null {

	const [player, setPlayer] = useState(undefined as TeamInterface | null | undefined);
	const updateProfile = useCallback(() => GET({ url: '/player/' + props.id })
		.then(setPlayer)
		.catch(() => setPlayer(null)), [setPlayer, props.id]);
	useEffect(() => {
		updateProfile();
	}, [updateProfile]);

	const [active, inactive] = useMemo(() => {
		let arr = [[], []] as [EnhancedPlayer[], EnhancedPlayer[]];
		if (!player) return arr;
		for (let p of player.tournaments) {
			if (p.active && p.tournament.active) arr[0].push(p);
			else arr[1].push(p);
		}
		return arr;
	}, [player]);
	const [showActive, toggleActive] = useState(true);
	const [showInactive, toggleInactive] = useState(!active.length);
	const [playerNames, setPlayerNames] = useState({});
	const activeTournaments = useMemo(() => active.map((v) => {
		let [elem, shouldFetch] = renderTournament(v, showActive, playerNames);
		if (shouldFetch.length) fetchNames(shouldFetch).then(names => setPlayerNames(names));
		return elem;
	}), [showActive, active, playerNames, setPlayerNames]);
	const inactiveTournaments = useMemo(() => inactive.map((v) => {
		let [elem, shouldFetch] = renderTournament(v, showInactive, playerNames);
		if (shouldFetch.length) fetchNames(shouldFetch).then(names => setPlayerNames(names));
		return elem;
	}), [showInactive, inactive, playerNames, setPlayerNames]);

	const notes = useMemo(() => {
		return Object.entries(player?.meta || {}).map(([k, v]) => {
			if (!v) return null;
			k = k.trim();
			if (k.endsWith('?')) k = k.slice(0, -1);
			if (typeof v === 'boolean') return k;
			k += ': ';
			return k + v.toString();
		}).filter(v => v);
	}, [player]);

	if (player === undefined) return null;
	if (player === null) return <NotFound />;

	return <>
		<div id='main' className={'animated-' + props.location.state?.direction}>
			<div className={styles.container}>
				<div className={styles.title}>
					<div className={['container', styles.info].join(' ')}>
						<h1><span className='bold'>
							{player.name}
						</span></h1>						
						<div className={styles.details}>
							Live tournaments: {active.length}
							{player.nationality ? <div className={styles.flag}>
								<img src={flags.replace('|', player.nationality)} alt={player.nationality}/>
								{countries[player.nationality as keyof typeof countries]}
							</div> : null}
						</div>
						<div className={[styles.contact].join(' ')}>
							<Contact player={player} notes={notes} />
						</div>
					</div>
				</div>
				<SectionBreak name='ACTIVE TOURNAMENTS' expanded={showActive} toggle={toggleActive} />
				{activeTournaments}
				<SectionBreak name='INACTIVE TOURNAMENTS' expanded={showInactive} toggle={toggleInactive} />
				{inactiveTournaments}
			</div>			
		</div>
	</>
}